    font-size: 1.8rem;
    padding: 20px 35px 20px 35px;
    margin-top: 24px;
    color: white;
    border-radius: 2px;
    box-shadow: 4px 2px 17px 2px rgba(0,0,0,0.17);
    transition: all .3s ease-in-out;
    text-transform: uppercase;
    margin-right: 15px;
}

